*{     
    margin:0 ;
    padding: 0; 
    box-sizing: border-box 
}
 :root{
    --tw-ring-color: rgba(59,130,246,.5);
    --tw-bg-opacity: 1;
    
 }     
        

html,body{
color: aliceblue;
height: 100vh;
width: 100vw;
    }

.main{
    /* background-color: rgb(21, 18, 41); */
    width: 100vw;
    background-color: rgb(74 78 105/var(--tw-bg-opacity));
    min-height: 100vh;

}
.main_warpper{
    width: 85vw;
    
    margin: 0 auto;
    display: flex;  
    align-items: center;
    flex-direction: column;

}
.main_heading{
    font-size: 20px;
 
    color: aliceblue;
    width: 100vw;
    text-align: center;
    background-color: rgb(34 34 59/var(--tw-bg-opacity));
    text-transform: uppercase;
    padding:13px 20px;
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 20px;

}
.cards{
    background-color: rgb(74 78 105/var(--tw-bg-opacity));
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2.5%;
    padding: 20px 20px;
    border-radius: 10px;
    
}
.card{
    width: 30%;
    border: 1px solid black;
    background-color: rgb(34 34 59/var(--tw-bg-opacity));
    border-radius: 10px;
    margin-top: 20px;

}
.image{
    /* border-radius: 10px; */
    border-radius: 10px 10px 0 0;

}
.info{
    padding: 10px 10px ;

}
.read_more{
    cursor: pointer;
    font-size: 10px;
    color: rgb(5, 207, 207);
}
.removebtn{
    margin:10px 10px ;
    text-transform: capitalize;
    padding: 5px 5px;
    border-radius: 8px;
    color:snow;
    /* background-color: aliceblue; */
    background-color: black;
    border: 1px snow solid;
}
.title{
    font-size: 1rem;
    font-weight: bold;
}
.description{
    font-size: .9rem;
    

}
@media screen and (max-width: 600px) {
   

    .cards{
        background-color: rgb(74 78 105/var(--tw-bg-opacity));
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        gap: 2.5%;
        padding: 20px 20px;
        border-radius: 10px;
        
    }
    .card{
        width: 80%;
        border: 1px solid black;
        background-color: rgb(34 34 59/var(--tw-bg-opacity));
        border-radius: 10px;
        margin-top: 20px;
    
    }
  }
  @media screen and (max-width: 800px) {
    
    .cards{
        background-color: rgb(74 78 105/var(--tw-bg-opacity));
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        gap: 2.5%;
        padding: 20px 20px;
        border-radius: 10px;
        
    }
    .card{
        width: 70%;
        border: 1px solid black;
        background-color: rgb(34 34 59/var(--tw-bg-opacity));
        border-radius: 10px;
        margin-top: 20px;
    
    }
  }